import React from 'react';
import aboutImage from '../Assets/images/about.png'
import '../About/About.css'

function About() {
    return (
        <div id="about">
            <div className='about-image'>
                <img src={aboutImage} alt=""></img>
            </div>
            <div className='about-text'>
                <h1>LEARN MORE ABOUT US</h1>
                <p>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Minima deserunt maiores iusto aliquam corporis inventore illo expedita nulla est! Officiis placeat rem dolore molestias deserunt quibusdam animi maiores temporibus libero?</p>
                <button>READ MORE</button>
            </div>            
        </div>
    )
}

export default About;
